import React, { useState, useEffect } from 'react';
import PostingService from '../services/PostingService';

const CreatePost = ({ closePosting }) => {
    const [post, setPost] = useState({});
    const [attachment, setAttachment] = useState();
    const [images, setImages] = useState([]);
    const formData = new FormData();

    const [categories, setCategories] = useState([]);
    const [models, setModels] = useState([]);

    const [category, setCategory] = useState("");
    useEffect(() => {
        setPost(
            {
                user_num: 1,
                status: "",
                post_title: "",
                model_name: "iphone XE",
                grade: "",
                price: 0,
                post_content: "",
            }
        )

        PostingService.getCategory().then((res) => { setCategories(res.data) })
        setModels([{ model_name: "  " }])
    }, [])

    useEffect(()=>{
            for(let i in categories){
                if (category == categories[i].category_name){
                    PostingService.getCategoriesModel(category).then((res) => {
                        setModels(res.data)
                            })
                    break
                }
            }
    },[category])


    const submit = () => {
        console.log(images)
        formData.append('post', new Blob([JSON.stringify(post)], {
            type: "application/json"
        }))
        if (images) {
            const imageLists = images

            for (let i = 0; i < imageLists.length; i++) {
                formData.append("files", imageLists[i])
            }
        }
        console.log(formData.get("files"))
        console.log(formData.get("post"))
        PostingService.createPost(formData)
    }

    const changeTitleHandler = (event) => {
        setPost({ ...post, post_title: event.target.value })
    }

    const changeModelNameHandler = (event) => {
        setPost({ ...post, model_name: event.target.value })
    }

    const changeGradeHandler = (event) => {
        setPost({ ...post, grade: event.target.value })
    }

    const changePriceHandler = (event) => {
        setPost({ ...post, price: event.target.value })
    }

    const changePostContentHandler = (event) => {
        setPost({ ...post, post_content: event.target.value })
    }

    const changeCategoryHandler = (event) => {
        setCategory(event.target.value)
        console.log(event.target.value)
    }

    const onFileChange = (event) => {
        const imageLists = event.target.files
        console.log(event.target.fi)
        setImages(imageLists)
        let imageUrlLists = []

        for (let i = 0; i < imageLists.length; i++) {
            const currentImageUrl = URL.createObjectURL(imageLists[i])
            imageUrlLists.push(currentImageUrl);
        }
        setAttachment(imageUrlLists)
        console.log(imageLists)

        const dataforclassify = new FormData();
        dataforclassify.append("files", imageLists[0])
        // PostingService.getClassify(dataforclassify).then((res) => {
        //     console.log(res)
        // })
    };

    useEffect(() => {
        if (!!models) {
          console.log(models)
        }
      }, [models]);

    return (
        <div className="modal">
            <div className="modal_body">
                <form>
                    <div className="modal-header">
                        <h1> </h1>
                    </div>
                    <hr />
                    <table className="form_table">
                        <tr>
                            <td colspan="2"> <input type="text" className="form_title" onChange={changeTitleHandler} /></td>
                        </tr>
                        <tr>
                            <td> <input className="form_category" type="text" list="category" onChange={changeCategoryHandler}></input><datalist id="category">
                                {
                                    categories.map((category) =>
                                        <option>{category.category_name}</option>
                                    )
                                }
                            </datalist>
                            </td>
                            <td>
                                 <input type="text" className="form_model" list="model" onChange={changeModelNameHandler}></input><datalist id="model">
                                    {
                                        models.map((model) =>
                                            <option>{model.model_name}</option>
                                        )
                                    }
                                </datalist>
                            </td>
                        </tr>
                        <tr>
                            <td> <input type="text" classNmae="form_grade" onChange={changeGradeHandler} /> <br />
                            </td>
                            <td> <input type="number" classNmae="form_price" onChange={changePriceHandler} /> <br />
                            </td>
                        </tr>
                    </table>

                    {
                        attachment &&
                        attachment.map(
                            (attachment) =>
                                <img src={attachment} style={{
                                    backgroundImage: attachment,
                                    width: 100,
                                    height: 100
                                }} />
                        )
                    }
                    <br />
                    
                    <div className="content_area">
                        
                        <br />
                        <input type="textarea" className="form_content" onChange={changePostContentHandler} /> <br />
                    </div>

                    <br /><br />
                    <div class="filebox">
                        <label for="attach-file"></label>
                        <input id="attach-file"
                            type="file"
                            accept="image/*"
                            onChange={onFileChange}
                            multiple="multiple"
                        />
                        <button onClick={submit}></button>
                        <button onClick={closePosting}></button>
                    </div>

                </form>
            </div>
        </div>
    );
}

export default CreatePost;