import React, { Componet, useState, useEffect } from 'react';
import PostingService from '../services/PostingService';
import GetImageView from './GetImageView';

const UpdatePost = ({ post_num, close }) => {
    const [post, setPost] = useState({});
    const [attachment, setAttachment] = useState();
    const [viewStat, setViewStat] = useState(false);
    const [viewSrc, setViewSrc] = useState("")
    useEffect(() => {
        PostingService.getSinglePost(post_num).then((res) => {
            setPost(res.data)
            setAttachment(res.data.picture_location)
            console.log(res.data)
        })

    }, [])

    const imgClicked = (e) => {
        console.log(e.target.src)
        setViewSrc(e.target.src)
        setViewStat(true)
    }

    const closeView = () => {
        setViewStat(false)
        setViewSrc("")
    }

    const onHide = () => {
        PostingService.hidePost.then(res => 
            console.log(res)
        )
    }

    const onExposure = () => {
        PostingService.exposurePost.then(res => 
                console.log(res)
            )
    }

    const onDelete = () => {
        PostingService.deletePost.then(res => 
                console.log(res)
            )
    }
    return (
        <div className="modal">
            <div className="modal_body">
                <form>
                    <div className="modal-header">
                        <h1> </h1>
                    </div>
                    <hr />
                    <table className="form_table">
                        <tr>
                            <td>: {post.post_title}</td>
                            <td>: {post.user_num}</td>
                        </tr>
                        <tr>
                            <td>: {post.category_name}
                            </td>
                            <td>
                                : {post.model_name}
                            </td>
                        </tr>
                        <tr>
                            <td>: {post.grade} <br />
                            </td>
                            <td>: {post.price} <br />
                            </td>
                        </tr>
                    </table>

                    {
                        attachment &&
                        attachment.map(
                            (attachment) =>
                                <img src={attachment} style={{
                                    backgroundImage: attachment,
                                    width: 100,
                                    height: 100
                                }}
                                    onClick={imgClicked}
                                />
                        )
                    }
                    <br />
                    
                    <div className="content_area">

                        <div className="content_box">{post.post_content}</div>
                        <br />
                        <br />
                    </div>

                    <br /><br />
                    <div class="filebox">
                        &nbsp;
                        {post.status == "" ?
                            <button onClick={onHide}></button>
                            :
                            <button onClick={onExposure}></button>
                        }
                        &nbsp;
                        <button onClick={onDelete}></button>
                        &nbsp;
                        <button onClick={close}></button>
                    </div>

                </form>
                {
                    viewStat && <GetImageView src={viewSrc} closeView={closeView} />
                }
            </div>
        </div>
    );
}

export default UpdatePost;